/* eslint-disable react-hooks/exhaustive-deps */
import React from 'react'
import {Link} from "react-router-dom"
import {useSelector} from "react-redux"
import { useEffect, useState } from "react"
import { init } from 'emailjs-com';
import emailjs from 'emailjs-com';
init("user_yxMunz9wCBlqf6LWvgsop");
const QuizEnd = () => {
    const answers=["Rajendra Prasad","15th August , 1947","Agra","26th Jan","Nehru","Delhi","West Bengal","Mountbatten","Gandhi","2nd , October"]
    const ans=useSelector(state=>state.changeAnswer)
    const eid=useSelector(state=>state.changeEmail)
    const [score,setScore]=useState(0)
    const sendemail=(templateParams)=>{
        emailjs.send('gmailid', 'templateid123', templateParams)
    .then(function(response) {
       console.log('SUCCESS!', response.status, response.text);
    }, function(error) {
       console.log('FAILED...', error);
    });

    }
    useEffect(()=>{
        var s=0;
        for(let i=0;i<10;i++)
        {
            if(ans[i]===answers[i])
            {
                s++;
            }
        }
        setScore(s)
        let templateParams = {
            result:s,
            receiver:eid
        };
        if(eid!=="none")
        {
            sendemail(templateParams)
        }
    },[])
    return (
        <div className="text-center text-dark bounce-in-bck" style={{paddingTop:'20vh',overflow:"hidden"}}>
            <h1>The Quiz has Ended</h1>
            <h2 className="p-3">Thank You for participating</h2>
            <h3 className="p-3">Your Score is : {score}</h3>
            <h3 className="p-3">The result is also sent to your email if you have given it.</h3>
            <Link to="/" className="p-3">Go Back to Home Page</Link>
            <p>Clear Browser Cookies to give quiz again since it uses local storage for preservation of time and progress.</p>
        </div>
    )
}
export default QuizEnd